import gsap from "gsap";
import { Container, Rectangle, Sprite, Texture } from "pixi.js";
import { gameSettings } from "../../../../core/BaseGame";
import { EBonus } from "../../GameViewState";
import { BonusIntroLetter } from "./BonusIntroLetter";

export abstract class BaseBonus extends Container {
    protected bound: Rectangle = new Rectangle();
    protected back: Sprite;
    protected letters: BonusIntroLetter[];
    protected getPoint?: () => any;
    constructor() {
        super();
        this.back = Sprite.from(Texture.WHITE);
        this.back.anchor.set(0.5,0.5);
        this.back.width = gameSettings.originalWidth;
        this.back.height = gameSettings.originalHeight;
        this.addChild(this.back);
        const letters = this.getLetters();
        this.letters = [];
        for (let i = 0; i < letters.length; i++) {
            const item = new BonusIntroLetter();
            item.visible = false;
            this.addChild(item);
            this.letters.push(item);
        }
    }  
    public async start(getPoint?: () => any): Promise<any> {
        this.getPoint = getPoint;
        this.visible = true;
        this.back.alpha = 0;
        const letters = this.getLetters();
        const amounts: number[] = [];
        let l = 0;
        for (let i = 0; i < letters.length; i++) {
            l++;
            if (letters[i] === " ") {
                amounts.push(l);
                l = 0;
            }
        }
        if (amounts.length === 0) {
            amounts.push(letters.length);
        } else {
            amounts.push(l);
        }
        return new Promise<void>((resolve) => {
            gsap.to(this.back, {alpha: 0.7, duration: 1});
            let counter = 0;
            let angle = -30;
            let currentLength = amounts[counter];
            let dAngle = 60 / (currentLength - 1);
            let w = 80 * currentLength;
            let x = -w*0.5;
            let dx = w / (currentLength - 1);
            let y = -400;
            const items = [];
            for (let i = 0; i < letters.length; i++) {
                const item = this.letters.pop();
                items.push(item);
                item.visible = true;
                item.letter = letters[i];
                item.position.set(-400, 700);
                item.scale.set(0.1, 0.1);
                item.angle = angle - 30;
                gsap.to(item, {angle, x, y, delay: i*0.1, duration: 0.5});
                gsap.to(item.scale, {x: 1, y: 1, delay: i*0.1, duration: 0.5});
                if (letters[i] === " ") {
                    counter++;
                    angle = -30;
                    currentLength = amounts[counter];
                    dAngle = 60 / (currentLength - 1);
                    w = 80 * currentLength;
                    x = -w*0.5;
                    y += 100;
                    dx = w / (currentLength - 1);
                } else {
                    angle += dAngle;
                    x += dx;
                }
                
            }
            gsap.to(this.back, {alpha: 0, delay: 1.2, duration: 0.5});
            gsap.delayedCall(1 + (letters.length-1)*0.1, ()=> {
                this.animate(items).then(()=>{
                    resolve();
                });
            });
            
        });
    }
    public clean(): void {
        this.getPoint = undefined;
        gsap.killTweensOf(this.back);
    }
    public goOut(): Promise<void> {
        return;
    }  
    public abstract get activeBound(): Rectangle[];  
    protected abstract getLetters(): string[];
    protected abstract animate(items: BonusIntroLetter[]): Promise<void>;
}