import gsap from "gsap";
import { Container, Graphics, NineSlicePlane, Sprite, Texture } from "pixi.js";

export class BoomMeter extends Container {
    private bar: Sprite;
    private max: number;
    private current: number;
    private initialWidth: number = 100;
    private barMask: Sprite;
    constructor() {
        super();
        const back = new Graphics();
        back.lineStyle(4,0x2ab909,1);
        back.drawRect(0,0,this.initialWidth, 40);
        this.addChild(back);

        this.bar = Sprite.from(Texture.WHITE);
        this.bar.width = this.initialWidth;
        this.bar.height = 40;
        this.bar.tint = 0x2ab909;
        this.addChild(this.bar);

        this.barMask = new Sprite(Texture.WHITE);
        this.barMask.width = 0;
        this.barMask.height = this.bar.height;
        this.addChild(this.barMask);

        this.bar.mask = this.barMask;

        this.current = 0;
        this.max = 10;
    }
    public set maximum(m: number) {
        this.max = m;
    }
    public reset() {
        gsap.killTweensOf(this.barMask);
        this.current = 0;
        gsap.to(this.barMask, {width: 0, duration: 0.1});
    }
    public update(amount: number): boolean {
        //console.log(amount);
        if (amount < 2) {
            amount = 0;
        } else {
            this.current += amount;
            this.current = Math.min(this.current, this.max);
        }
        const wasEnd = this.current >= this.max;
        gsap.killTweensOf(this.barMask);
        gsap.to(this.barMask, {width: this.initialWidth * (this.current / this.max), duration: 0.2});
        if (wasEnd) {
            this.current = 0;
            gsap.to(this.barMask, {width: 0, delay: 0.2, duration: 0.2});
        }
        return wasEnd;
    }
}