import { Sprite, Texture } from "pixi.js";
import { gameSettings, METER_SIZE, world } from "../../../core/BaseGame";
import gsap from "gsap";
import { Body, Circle, Shape } from "p2";
import { PhysicsSprite } from "../../../core/PhysicsSprite";
import { GlowFilter } from "@pixi/filter-glow";

export class CircleBlock extends PhysicsSprite {
    public static goalType = 1000;
    public static endType = 500;
    public sameColorNeighbors: Map<CircleBlock, boolean> = new Map<CircleBlock, boolean>();
    protected stepsAmount = 3;
    protected static scaleFactor = 0.5;
    public steps = 1;
    private _kinematic = false;
    private busy = false;
    private sensorShape: Shape;

    constructor(x: number, y: number, type: number, create: boolean = true) {
        super(type);
        this.position.set(x,y);
        if (create) {
            this.createBody(Body.DYNAMIC);
        }
        
    }
    public get kinematic() {
        return this._kinematic;
    }
    public set kinematic(b: boolean) {
        this._kinematic = b;
        if (b) {
            this.filters = [new GlowFilter({color: 0x65e4ff, quality: 0.5, outerStrength: 2})];
        } else {
            this.filters = [];
        }
        
    }
    override createBody(physType: 1 | 2 | 4): void {
        super.createBody(physType);
        this.sensorShape = new Circle({radius: (this.getRadius() + 5) / METER_SIZE});
        this.sensorShape.sensor = true;
        this.sensorShape.material = gameSettings.material;
        (this.sensorShape as any).data = true;
        this.body.addShape(this.sensorShape);
    }
    public animateAndDestroy() {
        gsap.killTweensOf(this.view.scale);
        gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.clean();
        }});
        this.destroyBody();
    }
    override clean(): void {
        this.busy = false;
        super.clean();
    }
    public boom(fromBoom: boolean = true) {
        return this.increase(this.stepsAmount-this.steps, fromBoom);
    }
    protected getRadius() {
        return 35;
    }
    protected createView() {
        const v = new Sprite(Texture.from(`${this.type}.png`));
        v.scale.set(0.5,0.5);
        return v;
    }
    override createShape(): Shape {
        const shape = new Circle({radius: this.getRadius() / METER_SIZE});
        shape.material = gameSettings.material;
        return shape;
    }
    
    public increase(s: number, fromBoom: boolean = false): number {
        if (this.busy || !this.body) return 0;
        let killAmount = 0;
        this.busy = true;
        s += Math.max(0, this.sameColorNeighbors.size - 1);
        this.steps += s;
        if (this.steps >= this.stepsAmount) {
            if (fromBoom) {
                killAmount = 1;
            }
            gsap.killTweensOf(this.view.scale);
            this.sameColorNeighbors.forEach((n,k)=> {
                if (k !== this) {
                    const a = k.increase(this.stepsAmount-k.steps, fromBoom);
                    if (fromBoom) {
                        killAmount += a;
                    }
                }
            });
            gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
                this.clean();
            }});
            this.destroyBody(fromBoom);
        } else {
            const i = CircleBlock.scaleFactor * this.steps;
            gsap.to(this.view.scale, {x: i, y: i, duration: 0.2, onUpdate: ()=> {
                (this.shape as Circle).radius = this.getRadius() / CircleBlock.scaleFactor * this.view.scale.x / METER_SIZE;
                this.shape.updateBoundingRadius();
                (this.sensorShape as Circle).radius = (this.getRadius() + 5) / CircleBlock.scaleFactor * this.view.scale.x / METER_SIZE;
                this.sensorShape.updateBoundingRadius();
                this.body.updateBoundingRadius();
                this.body.updateMassProperties();
                this.busy = false;
            }});
        }
        return killAmount;
    }
}