import { Button } from "../../../core/ui/Button";
import { Sprite, Text, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";

export class ChatItem extends Button {
    public id: number;
    constructor(id: number, name: string, message: string, grey: boolean) {
        super("", "");
        this.id = id;
        this.btn.anchor.set(0,0);

        const txt = new Text(name, {fontFamily: "Arial", fontSize: 40, fill: 0x000000, fontWeight: "bold"});
        txt.position.set(20, 10);
        this.addChild(txt);

        const txt1 = new Text(message, {fontFamily: "Arial", fontSize: 33, fill: 0x000000});
        txt1.position.set(20, 5 + txt.height + 15);
        this.addChild(txt1);

        this.btn.tint = grey ? 0xCCCCCC : 0xFFFFFF;
        this.btn.width = gameSettings.originalWidth - 30;
        this.btn.height = 5 + txt.height + 20 + txt1.height + 5;
    }
    /*public set text(t: string) {
        this.txt.text = t;
    }*/
}