import { Container, Sprite, Text, Texture } from "pixi.js";

export class BonusIntroLetter extends Container {
    private txt: Text;
    constructor() {
        super();
        const back = Sprite.from(Texture.WHITE);
        back.anchor.set(0.5, 0.5);
        back.width = 80;
        back.height = 80;
        back.tint = 0xcacaca;
        this.addChild(back);

        this.txt = new Text("", {fontWeight: "bold", fontSize: 70});
        this.txt.anchor.set(0.5, 0.5);
        this.addChild(this.txt);
    }
    public set letter(l: string) {
        this.txt.text = l;
    }
}