import gsap from "gsap";
import { Rectangle, Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseBonus } from "./BaseBonus";
import { BonusIntroLetter } from "./BonusIntroLetter";

export class LaserLettersBonus extends BaseBonus {
    private rect: Rectangle;
    laser: Sprite;
    constructor() {
        super();
        this.laser = Sprite.from(Texture.WHITE);
        this.laser.width = 500;
        this.laser.height = 10;
        this.laser.tint = 0x4d9aff;
        this.laser.alpha = 0;
        this.laser.anchor.set(0.5,0.5);
        this.addChild(this.laser);
        this.laser.visible = false;
    }
    public get activeBound(): Rectangle[] {
        if (this.rect) {
            return [this.rect];
        }
        return undefined;
    }
    protected getLetters(): string[] {
        return ["{", "}"];
    }
    protected animate(items: BonusIntroLetter[]): Promise<void> {
        return new Promise<void>((resolve)=> {
            if (this.getPoint || items.length !== 2) {
                const yy = this.getPoint();
                gsap.to(items[0], {x: -300, angle: 0, y: yy * METER_SIZE, duration: 0.2 });
                gsap.to(items[1], {x: 300, angle: 0, y: yy * METER_SIZE, duration: 0.2 });
                gsap.delayedCall(0.2, ()=> {
                    this.laser.visible = true;
                    this.laser.y = yy * METER_SIZE;
                    this.rect = new Rectangle(-7, yy-0.5, 14, 1);
                    gsap.to(this.laser, {alpha: 0.6, duration: 0.1 });
                    gsap.delayedCall(0.1, ()=> {
                        this.rect = undefined;
                        gsap.to(items[0], {alpha: 0, duration: 0.1 });
                        gsap.to(items[1], {alpha: 0, duration: 0.1 });
                        gsap.to(this.laser, {alpha: 0, duration: 0.1 });
                        gsap.delayedCall(0.1, ()=> {
                            resolve();
                        });
                    });
                })
            } else {
                resolve();
            }
        });
    }
    
}