import gsap from "gsap";
import { Shape, Box } from "p2";
import { Sprite, Text, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseObstacle } from "./BaseObstacle";

export class RandomObstacle extends BaseObstacle {
    private moving: boolean = false;
    private lives: number;
    private txt: Text;
    constructor(pos: [number, number], lives: number) {
        super(pos[0], pos[1], 100, 100, 0);
        this.type = 251;
        this.lives = lives;
        this.txt = new Text(this.lives.toString(), {fontSize: 10, fill: 0x000000, align: "center"});
        this.txt.anchor.set(0.5,0.5);
        this.view.addChild(this.txt);
    }
    public changePosition() {
        if (this.moving) return;
        this.lives--;
        const finish = this.lives <= 0;
        this.moving = true;
        const s = this.view.scale.x;
        gsap.to(this.view.scale, {x: 0.01, y: 0.01, duration: 0.1, onUpdate: ()=> {
            (this.shape as Box).width = (this.shape as Box).height = 100 * this.view.scale.x / METER_SIZE;
            this.shape.updateBoundingRadius();
            this.body.updateBoundingRadius();
            this.body.updateMassProperties();
        }, onComplete: ()=> {
            if (finish) {
                this.clean();
            } else {
                this.txt.text = this.lives.toString();
                const rx = Math.random()*9 - 4.5;
                const ry = -Math.random()*5;
                this.body.position = [rx, ry];
                gsap.to(this.view.scale, {x: s, y: s, duration: 0.4, onUpdate: ()=> {
                    (this.shape as Box).width = (this.shape as Box).height = 100 * this.view.scale.x / METER_SIZE;
                    this.shape.updateBoundingRadius();
                    this.body.updateBoundingRadius();
                    this.body.updateMassProperties();
                }, onComplete: ()=> {
                    this.moving = false;
                }});
            }
        }});
        
    }
    protected createView() {
        const view = new Sprite(Texture.WHITE);
        view.anchor.set(0.5);
        view.tint = 0x777777;
        return view;
    }
    override createShape(): Shape {
        const shape = new Box({width: this.view.width/METER_SIZE, height: this.view.height/METER_SIZE});
        (shape as any).data = true;
        return shape;
    }
}