import gsap, { Bounce, Elastic, Linear } from "gsap";
import { Container, Sprite, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";

export class Logo extends Container {
    private explosion: Sprite;
    private face: Sprite;
    private logo: Sprite;
    constructor() {
        super();
        this.explosion = Sprite.from(Texture.from("splash_explosion.png"));
        this.explosion.anchor.set(0.5);
        this.explosion.scale.set(0, 0);
        this.explosion.position.y = -gameSettings.originalHeight * 0.5+516;
        this.explosion.alpha = 0;
        this.addChild(this.explosion);

        this.face = Sprite.from(Texture.from("splash_logo_face.png"));
        this.face.position.set(-131, -350);
        this.face.alpha = 0;
        this.addChild(this.face);

        this.logo = Sprite.from(Texture.from("splash_logo.png"));
        this.logo.anchor.set(0.5);
        this.logo.position.set(0, -260);
        this.logo.scale.set(3, 3);
        this.logo.alpha = 0;
        this.addChild(this.logo);
    }
    public start() {
        this.explosion.position.y = -gameSettings.originalHeight * 0.5+516;
        this.explosion.scale.set(0, 0);
        this.explosion.alpha = 0;
        this.face.position.set(-131, -350);
        this.face.alpha = 0;
        this.logo.position.set(0, -260);
        this.logo.scale.set(3, 3);
        this.logo.alpha = 0;

        gsap.to(this.explosion, {alpha: 1, duration: 0.2});
        gsap.to(this.explosion.scale, {x: 1, y: 1, duration: 1, ease: Elastic.easeOut});

        gsap.to(this.logo, {alpha: 1, delay: 0.2, duration: 0.2});
        gsap.to(this.logo.scale, {x: 1, y: 1, delay: 0.2, duration: 1, ease: Bounce.easeOut});

        gsap.to(this.face, {alpha: 1, delay: 1.09, duration: 0.1});
        gsap.to(this.face, {y: -448, delay: 1.1, duration: 2, ease: Linear.easeNone});
    }
    public stop() {
        gsap.killTweensOf(this.explosion);
        gsap.killTweensOf(this.explosion.scale);

        gsap.killTweensOf(this.logo);
        gsap.killTweensOf(this.logo.scale);

        gsap.killTweensOf(this.face);
    }
}