export enum EIAPBonus {
    None = 0,
    Kill,
    Freeze,
    RocketUp,
    RocketLeft,
    All
}
export enum EBonus {
    None = 0,
    BoomLetters,
    LaserLetters,
    BigBoomLetters,
    All
}
export enum EObstacle {
    None = 0,
    Random,
    Static
}
export interface IObstacle {
    type: EObstacle;
    position?: [number, number],
    lives?: number,
}
export interface ILevelData {
    name: string,
    blocks: number,
    startAmount: number,
    time: number,
    lines: number,
    boomMeter: number,
    bonuses: number[],
    obstacles: IObstacle[],
    starsConditions: number[],
    score?: number
}

