    public async start(...params: any) {
        gameplayStart();
        this.boltsActive = false;
        this.score = 0;
        this.scorePlace.score = 0;
        this.levelId = params[0][0] as number;
        this.activeBonus = EBonus.None;
        this.randomBodiesPositions = [];
        this.obstacles = [];
        this.powerups = [2, 3];
        for (let i = 0; i < this.powerupButtons.length; i++) {
            this.powerupButtons[i].amount = this.powerups[i];
        }
        this.isTransition = false;
        
        this.nextType = undefined;
        this.nextLines = [];
        this.isActivePowerup = false;
        this.generateLineBlocks();
        this.tick = 0;
        this.maxY=-800;
        this.moving = false;
        sendEvent("start", "game", "level");
        sendProgressionEvent(EGAProgressionStatus.Start);
        gameSettings.paused = false;

        await this.generateStartBlocks();
        //this.addChild(this.nextTimer);
        this.boomMeter.maximum = this.currentLevelData.boomMeter;
        //this.addChild(this.boomMeter);
        this.addActiveBlock();

        app.stage.on("pointerup", this.onClick, this);
        app.stage.on("pointerdown", this.onTapStart, this);
        //Ticker.shared.add(this.update, this);
    }
    public unusedBlocksRemoved(amount: number, startPosition?: ObservablePoint) {
        if (this.isTransition) return;
        let score = amount * 5;
        if (amount >= 5) {
            if (startPosition) {
                this.fireMissle(1, startPosition);
                score += 50;
            }
        }
        this.addScore(score);
        if (this.activeBonus) return; 
        const boom = this.boomMeter.update(amount);
        if (this.levelId > 1) this.playBolts(amount, startPosition);
        if (boom) {
            const bonus = this.getRandomBonus();
            this.setActiveBonus(bonus);
        }
    }
    private async playBolts(amount: number, startPosition: ObservablePoint) {
        if (amount < 2 || !startPosition) return;
        const time = 0.5;
        this.boltsActive = true;
        amount = Math.min(amount, 6);
        for (let i = 0; i < amount; i++) {
            const bolt = new Sprite(Texture.from(`bolt.png`));
            bolt.anchor.set(0.5, 0.5);
            bolt.scale.set(0.1, 0.1);
            bolt.position.copyFrom(startPosition);
            this.gameContainer.addChild(bolt);
            gsap.to(bolt.scale, {x: 1.5, y: 1.5, duration: time * 0.5, ease: Linear.easeNone});
            gsap.to(bolt.scale, {x: 0, y: 0, delay: time * 0.5, duration: time * 0.5, ease: Linear.easeNone});
            gsap.to(bolt.position, {x: 270, y: -680, duration: time, ease: Linear.easeNone, onComplete: ()=> {
                this.gameContainer.removeChild(bolt);
            }});
            await this.delay(0.1);
        }
        this.boltsActive = false;
    }
    private addScore(score: number) {
        this.score += score;
        this.scorePlace.score = this.score;
    }
    private get currentLevelData(): ILevelData {
        return gameSettings.levels[this.levelId] as ILevelData;
    }
    private getRandomPosition() {
        if (this.isTransition) return undefined; 
        const i = Math.floor(Math.random()*this.randomBodiesPositions.length);
        let p = this.randomBodiesPositions[i];
        this.randomBodiesPositions.splice(i, 1);
        if (Math.abs(p[1]) >= 20) {
            return undefined;
        }
        return p;
    }
    private async setActiveBonus(bonus: EBonus) {
        this.activeBonus = bonus;
        this.nextTimer.freeze();
        this.addScore(100);
        let getPoint = undefined;
        switch(bonus) {
            case EBonus.BoomLetters:
            case EBonus.BigBoomLetters:
                this.meterBonus = (bonus === EBonus.BoomLetters) ? new BoomLettersBonus() : new BigBoomLettersBonus();
                getPoint = ()=> {
                    return this.getRandomPosition();
                };
                break;
            case EBonus.LaserLetters:
                this.meterBonus = new LaserLettersBonus();
                getPoint = ()=> {
                    const p = this.getRandomPosition();
                    if (!p) {
                        return undefined;
                    }
                    return p[1];
                };
                break;
            default: return;
        }
        this.frontContainer.addChild(this.meterBonus);

        await this.meterBonus.start(getPoint);
        this.clearMeterBonus(false);
        this.nextTimer.unFreeze();
    }

