import { Box, Shape } from "p2";
import { Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseObstacle } from "./BaseObstacle";

export class StaticObstacle extends BaseObstacle {
    constructor(pos: [number, number]) {
        super(pos[0], pos[1], 100, 100, 0);
        this.type = 250;
    }
    protected createView() {
        const view = new Sprite(Texture.WHITE);
        view.anchor.set(0.5);
        view.tint = 0xBBBBBB;
        return view;
    }
    override createShape(): Shape {
        const shape = new Box({width: this.view.width/METER_SIZE, height: this.view.height/METER_SIZE});
        (shape as any).data = true;
        return shape;
    }
}