import gsap from "gsap";
import { AnimatedSprite, Container, NineSlicePlane, Sprite, Texture } from "pixi.js";

export class NextBlocksTimer extends Container {
    private bar: NineSlicePlane;
    private barMask: Sprite;
    private initialWidth: number = 600;
    public isFrozen: boolean;
    private delay: gsap.core.Tween;
    private bell: AnimatedSprite;
    constructor() {
        super();
        const back = new NineSlicePlane(Texture.from("progress_back.png"),6,4,6,4);
        back.width = this.initialWidth;
        back.height = 15;
        this.addChild(back);

        this.bar = new NineSlicePlane(Texture.from("progress_bar.png"), 6,4,6,4);
        this.bar.width = this.initialWidth;
        this.bar.height = 15;
        this.addChild(this.bar);

        this.barMask = new Sprite(Texture.WHITE);
        this.barMask.width = 0;
        this.barMask.height = this.bar.height;
        this.addChild(this.barMask);
        this.bar.mask = this.barMask;

        this.bell = new AnimatedSprite([Texture.from("bell0.png"), Texture.from("bell1.png")], true);
        this.bell.animationSpeed = 0.1;
        this.bell.anchor.set(0.5, 0.5);
        this.bell.position.set(440,21);
        this.addChild(this.bell);
        this.bell.loop = true;
        this.bell.visible=false;
    }
    public setProgress(p: number) {
        this.barMask.width = this.initialWidth * p;
        if (p === 1) {
            this.bell.play();
            gsap.delayedCall(2, ()=> {
                this.bell.gotoAndStop(0);
            });
        }
    }
    public unFreeze() {
        this.isFrozen = false;
    }
    public freeze(seconds: number = -1) {
        this.isFrozen = true;
        if (this.delay) {
            this.delay.kill();
        }
        if (seconds === -1) return;
        this.delay = gsap.delayedCall(seconds, ()=> {
            this.isFrozen = false;
        });
    }
    public clear() {
        if (this.delay) {
            this.delay.kill();
            this.delay = undefined;
        }
        this.isFrozen = false;
    }
}