import { DEG_TO_RAD } from "@pixi/math";
import { Sprite } from "@pixi/sprite";
import { Body, Box, Shape } from "p2";
import { Texture } from "pixi.js";
import { METER_SIZE } from "../../../core/BaseGame";
import gsap from "gsap";
import { PhysicsSprite } from "../../../core/PhysicsSprite";

export class StaticBlock extends PhysicsSprite {
    constructor(x: number, y: number, width: number, height: number, rot: number) {
        super(0);
        this.position.set(x,y);
        this.view.width = width;
        this.view.height = height;
        this.createBody(Body.STATIC);
        this.body.mass = 0;
        this.body.angle = DEG_TO_RAD * rot;
    }
    override createShape(): Shape {
        return new Box({width: this.view.width/METER_SIZE, height: this.view.height/METER_SIZE});
    }
    protected createView() {
        const view = new Sprite(Texture.WHITE);
        view.anchor.set(0.5);
        view.tint = 0x000000;
        view.visible = false;
        view.alpha = 0;
        return view;
    }
    public isObstacle() {
        return false;
    }
    public animateAndDestroy() {
        gsap.killTweensOf(this.view.scale);
        gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.clean();
        }});
        this.destroyBody();
    }
}