import { Container, NineSlicePlane, Texture, Text } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { CircleBlock } from "./CircleBlock";

export class Message extends Container {
    private back: NineSlicePlane;
    public blocks: CircleBlock[];
    private startX: number;
    private startY: number;
    private txt: Text;
    constructor() {
        super();
        this.blocks = [];
        this.back = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        this.back.width = gameSettings.originalWidth * 0.96;
        this.back.height = 300;
        this.back.position.set(-this.back.width * 0.5, 0);
        this.addChild(this.back);

        this.txt = new Text("From: Leopold",{fontSize: 25, fill: 0x000000, fontWeight: "bold", align: "left"});
        this.txt.anchor.set(0, 0.5);
        this.txt.position.set(-this.back.width * 0.5 + 40, 30);
        this.addChild(this.txt);
        this.clear();
    }
    public setLines(nextLines: number[][], name: string) {
        this.txt.text = "From: " + name;
        const lines = nextLines.length;
        this.back.height = lines * 75 + 70;
        this.startX = -300;
        this.startY = 95;

        for (let j = 0; j < lines; j++) {
            let xx = this.startX;
            for (let i = 0; i < nextLines[j].length; i++) {
                const f = new CircleBlock(xx,this.startY,nextLines[j][i],false);
                this.addBlock(f);
                xx += 74;
            }
            this.startY+=70;
        }
    }
    public addBlock(f: CircleBlock) {
        this.blocks.push(f);
        this.addChild(f);
        f.visible = false;
    }
    public show() {
        this.blocks.forEach((f)=> {
            f.visible = true;
        });
    }
    public clear() {
        this.blocks.forEach((f)=> {
            this.removeChild(f);
        });
        this.blocks = [];
    }
}