import gsap, { Bounce } from "gsap";
import { Container, Sprite, Text, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { TextedButton } from "../../../core/ui/TextedButton";
import { calculateStars } from "../../Game";

export class LevelFinished extends Container {
    private back: Sprite;
    private popup: Container;
    private callback: () => void;
    private scoreTxt: Text;
    private stars: Sprite[];
    constructor() {
        super();
        this.back = Sprite.from(Texture.WHITE);
        this.back.anchor.set(0.5, 0.5);
        this.back.width = gameSettings.originalWidth;
        this.back.height = gameSettings.originalHeight;
        this.back.tint = 0x000000;
        this.back.alpha = 0;
        this.addChild(this.back);

        this.popup = new Container();
        this.popup.scale.set(0,0);
        this.addChild(this.popup);

        const popupBack = Sprite.from(Texture.WHITE);
        popupBack.anchor.set(0.5, 0.5);
        popupBack.width = 600;
        popupBack.height = 900;
        popupBack.tint = 0xAAAAAA;
        this.popup.addChild(popupBack);

        const title = new Text("Congratulations!", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 70, fill: 0xFF0000, align: "center", fontWeight: "bold"})
        title.anchor.set(0.5, 0.5);
        title.position.y = -370;
        this.popup.addChild(title);

        this.scoreTxt = new Text("0", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 100, fill: 0x000000, align: "center", letterSpacing: 7, fontWeight: "bold"})
        this.scoreTxt.anchor.set(0.5, 0.5);
        this.popup.addChild(this.scoreTxt);

        this.stars = [];
        let xx = -150;
        for (let i = 0; i < 3; i++) {
            const star = Sprite.from(Texture.from("like.png"));
            star.anchor.set(0.5, 0.5);
            star.position.set(xx, 150);
            this.popup.addChild(star);
            this.stars.push(star);
            star.visible = false;
            xx += 150;
        }

        const okBut = new TextedButton("OK", 20, 0xFFFFFF, 0x000000);
        okBut.on("pointerup", ()=> {
            this.hide();
        });
        okBut.position.y = 350;
        this.popup.addChild(okBut);
    }
    public show(score: number, conditions: number[], callback: ()=>void) {
        for (let i = 0; i < this.stars.length; i++) {
            const star = this.stars[i];
            star.visible = true;
            star.scale.set(0,0);
            (star as any).showed = false;
        }
        this.callback = callback;
        this.visible = true;
        this.back.alpha = 0;
        this.popup.scale.set(0,0);
        //const stars = calculateStars(score, conditions);
        const o = {s: 0};
        this.scoreTxt.text = "0";
        gsap.to(o, {s: score, delay: 1, duration: 2, onUpdate: ()=> {
            this.scoreTxt.text = Math.round(o.s).toString();
            for (let i = 0; i < conditions.length; i++) {
                const star = this.stars[i];
                //console.log(i, star)
                if (o.s >= conditions[i] && !(star as any).showed) {
                    (star as any).showed = true;
                    gsap.to(star.scale, {x: 1, y: 1, duration: 0.2, ease: Bounce.easeOut});
                }
            }
        }});
        gsap.to(this.back, {alpha: 0.5, duration: 0.5});
        gsap.to(this.popup.scale, {x: 1, y: 1, duration: 1, ease: Bounce.easeOut});
    }
    private hide() {
        gsap.to(this.back, {alpha: 0, duration: 0.2});
        gsap.to(this.popup.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.visible = false;
            this.callback();
        }});
    }
}