import { Body, Box, Shape } from "p2";
import { Graphics, Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../core/BaseGame";
import { PhysicsSprite } from "../../../core/PhysicsSprite";
import gsap, {Cubic} from "gsap";

export class RocketPowerup extends PhysicsSprite {
    public rocket: boolean = true;
    private startX: number;
    private startY: number;
    private endX: number;
    private endY: number;
    constructor(startX: number, startY: number, endX: number, endY: number) {
        super(0);
        this.startX = startX;
        this.startY = startY;
        this.endX = endX;
        this.endY = endY;
        this.position.set(startX, startY);
        if (startX !== endX) this.view.angle = 90;

        this.createBody(Body.KINEMATIC);
    }
    override createShape(): Shape {
        const s = new Box({width: 100 / METER_SIZE, height: 100 / METER_SIZE});
        s.sensor = true;
        return s;
    }
    protected createView(): Sprite {
        return new Sprite(Texture.from(`rocket1.png`));
    }
    public async animate() {
        return new Promise<void>((r)=>{
            const s = {x: this.startX, y: this.startY};
            gsap.to(s, {x: this.endX, y: this.endY, duration: 1, ease: Cubic.easeIn, onComplete: ()=>{
                this.clean();
                r();
            }, onUpdate: ()=> {
                this.body.position = [s.x/METER_SIZE, s.y/METER_SIZE];
            }});
        });
    }
}